*{
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}


input {
  color: gray;
  font: size 1.2rem;
  width: 100%;
}


.card-grid {
  display: flex;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin: 0px 10px 10px 0px;
  overflow: hidden;
}


.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  width: 100%;
}